<template>
    <nav class="navbar navbar-expand-lg navbar-dark bg-dark">
    <div class="container-fluid"> <!-- kontener przy zmniejszeniu lista -->
      <router-link class="navbar-brand" to="/">Home</router-link>
      <button
        class="navbar-toggler"
        type="button"
        data-bs-toggle="collapse"
        data-bs-target="#navbarNav"
        aria-controls="navbarNav"
        aria-expanded="false"
        aria-label="Toggle navigation"
      >
        <span class="navbar-toggler-icon"></span>
      </button>
      <div class="collapse navbar-collapse" id="navbarNav">
        <ul class="navbar-nav">
          <li class="nav-item">
            <router-link class="nav-link" to="/video-list">Video List</router-link>
          </li>
          <li class="nav-item" v-if="!isLoggedIn">
            <router-link class="nav-link" to="/login">Log in</router-link>
          </li>
          <li class="nav-item">
            <router-link class="nav-link" to="/record-video">Record Video</router-link>
          </li>
          
        </ul>
      </div>
      <div > Settings </div>
    </div>
  </nav>
    <h2>Zarejestruj się</h2>
    <p><input type="text" placeholder="Imię" v-model="firstName" /></p>
    <p><input type="text" placeholder="Nazwisko" v-model="lastName" /></p>
    <p><input type="text" placeholder="Email" v-model="email" /></p>
    <p><input type="text" placeholder="Hasło" v-model="password" /></p>
    <p><button @click="register">Zarejestruj</button></p>
    <p>
        <button @click="registerWithGoogle">
            Zarejestruj się za pomocą konta Google
        </button>
    </p>
</template>

<script setup>
/* eslint-disable no-unused-vars */
import router from "@/router";
import { createUserWithEmailAndPassword, getAuth, GoogleAuthProvider, signInWithPopup } from "firebase/auth";
import { getFirestore, collection, doc, setDoc, serverTimestamp } from "firebase/firestore";
import { ref } from "vue";

const firstName = ref("");
const lastName = ref("");
const email = ref("");
const password = ref("");
const errMsg = ref("");

const register = () => {
  const auth = getAuth();
  const db = getFirestore();
  
  createUserWithEmailAndPassword(auth, email.value, password.value)
    .then((user) => {
      console.log("Pomyślnie dodano użytkownika: ", user.user.uid);
      
      const usersCollection = collection(db, "users");
      const userDoc = doc(usersCollection, user.user.uid);

      const userData = {
        firstName: firstName.value,
        lastName: lastName.value,
        email: email.value,
        createdAt: serverTimestamp(),
        updatedAt: serverTimestamp(),
      };
      
      setDoc(userDoc, userData)
        .then(() => {
          router.push("/");
          console.log("Użytkownik dodany")
        })
        .catch((error) => {
          console.log(error);
        });
    })
    .catch((error) => {
        console.log(error);
    });
};

const registerWithGoogle = () => {
    const db = getFirestore();
    const provider = new GoogleAuthProvider();
    
    signInWithPopup(getAuth(), provider)
        .then((result) => {
            const usersCollection = collection(db, "users");
            const userDoc = doc(usersCollection, result.user.uid);

            const user = result.user;
            const [firstName, lastName] = user.displayName.split(' ');
            const email = user.email;

            const userData = {
                firstName: firstName,
                lastName: lastName,
                email: email,
                createdAt: serverTimestamp(),
                updatedAt: serverTimestamp(),
            };
            
            setDoc(userDoc, userData)
                .then(() => {
                    router.push("/");
                })
                .catch((error) => {
                    console.log(error)
                });

            router.push("/");
        })
        .catch((error) => {
            console.log(error);
        })
};
</script>

<script>
export default {
  name: "RegisterForm",
};
</script>

